.box-skeleton,.line-skeleton {
    background-image: linear-gradient(90deg,#f4f4f4,#e5e5e5cc 40px,#f4f4f4 80px);
    background-size: 600px
}

.box-skeleton {
    background-color: #ccc;
    animation: shine-avatar 1s ease-out infinite;
    -webkit-animation: shine-avatar 1s ease-out infinite
}

.line-skeleton {
    border-radius: 6px;
    animation: shine-lines 1s ease-out infinite;
    -webkit-animation: shine-lines 1s ease-out infinite
}

.shimmer-anim {
    position: relative;
    background: #f4f4f4;
    overflow: hidden
}

.shimmer-anim:before {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    background-image: linear-gradient(90deg,#f4f4f4 0,#0000000d 20%,#f4f4f4 40%,#f4f4f4);
    background-repeat: no-repeat;
    background-size: 450px 400px;
    -webkit-animation: shimmer 1s linear infinite;
    animation: shimmer 1s linear infinite
}

.legalkart-faq {
    background: #17172c;
    padding: 50px
}

.legalkart-faq .talk-live-instant li a {
    display: inline-block
}

.legalkart-faq .accordion-sec {
    padding-left: 50px
}

.legalkart-faq .accordion-sec .accordion {
    width: 100%;
    border-bottom: 1px solid rgba(255,255,255,.4)
}

.legalkart-faq .accordion-sec .accordion-header {
    margin: 0;
    font-size: 16px;
    cursor: pointer;
    padding: 25px 20px 25px 0;
    position: relative;
    font-weight: 700;
    color: #ffff
}

.legalkart-faq .accordion-header.active::after {
    content: url(data:image/png;base64,);
    color: #fff;
    display: flex
}

.legalkart-faq .accordion-header.closed::after {
    content: url(data:image/png;base64,);
    color: #fff
}

.legalkart-faq .accordion-header::after {
    content: "+";
    font-weight: 400;
    position: absolute;
    right: 0;
    top: 26px;
    color: #fff;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    line-height: 17px
}

.legalkart-faq .accordion-sec .panel {
    display: none;
    padding-left: 0;
    padding-right: 0;
    padding-top: 0
}

.accordion-sec .accordion .panel ul li,.legalkart-faq .accordion-sec .accordion .panel p {
    color: rgba(255,255,255,.56);
    font-size: 14px;
    font-weight: 300;
    line-height: 25px
}

.legalkart-faq .accordion-sec .accordion .panel ul li {
    line-height: 16px
}

.legalkart-faq .frequentqa-sec .panel ul {
    /* list-style-image: url(../layout-optim/doc-review-consult/bullet-tick.png); */
    margin-left: 20px;
    margin-right: 20px
}
